
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '../../fetch/api'
import HlCell from '../../base/hl-cell/hl-cell'
import wx from 'weixin-js-sdk'
import { isWeiXin } from '../../util/util'
import { mapGetters, mapMutations } from 'vuex'

export default {
props: {
pagState: {
type: String,
default: '待付款'
},
shopName: {
type: String,
default: '3'
},
name: {
type: String,
default: ''
},
address: {
type: String,
default: '广东省广州市天河区棠下街道荷光路文星酒店'
},
orderTime: {
type: String,
default: '455'
},
orderNo: {
type: String,
default: ''
}
},
data () {
return {
detaileContan: '',
orderGuid: '',
orderType: 9,
qrcodeImg: '',
expiresIn: 0,
timer: null,
itemList: {},
vShopProductType: 0,
vShopProductGuid: ''
}
},
created () {

},
activated () {
this.signApi()
this.getOrder()
this.getQrcode()
},
mounted () {
this.signApi()


},
methods: {

getOrder () {

api.orderDetails({

vOrderGuid: this.$route.query.vOrderGuid
}).then(res => {
console.log('获取订单详情')
console.log(res)
if (res.code === 0) {
this.itemList = res.data
this.vShopProductGuid = res.data.vShopProductGuid
this.signApiShare()
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},
doPay (orderGuid) {
let that = this
isWeiXin(function () {
that.toPay(orderGuid)
}, function () {
let options2 = {
'orderType': that.orderType,
'payway': 2,
'orderGuid': orderGuid
}
let jsonStr = JSON.stringify(options2)
that.anzhuoPay(jsonStr)
})
},
anzhuoPay (jsonStr) {

this.$msgbox('提示', '请到微信端支付')

},
orderTimeFn (timeFn) {
return '交易时间：' + timeFn
},
orderNoFn (orderNo) {
return '订单编码：' + orderNo
},
commedTotal (total) {
return '共' + total + '件商品'
},
totalMenoy (fellMenoy) {
return '实付￥' + fellMenoy
},
consumCode (feelNum) {
return '消费码:' + feelNum
},
isPageState (orderStatus, itemCode) {
if (orderStatus === 1) {
return '待付款'
} else if (orderStatus === 2) {
if (itemCode === 2) {
return '待消费'
} else {
return '待发货'
}
} else if (orderStatus === 3) {
return '待收货'
} else if (orderStatus === 5) {
return '已完成'
} else if (orderStatus === 6) {
return '退款中'
} else if (orderStatus === 7) {
return '已退款'
} else if (orderStatus === 8) {
return '已关闭(过期)'
} else if (orderStatus === 9) {
return '组团中'
}
},
canell (orderGuid) {

let than = this
this.$confirm('提示', '是否确定取消该订单?', function () {
than.getCancelOder(orderGuid)
})
},
getCancelOder (orderGuid) {

api.cancelOrder({

vOrderGuid: orderGuid
}).then(res => {
let that = this
if (res.code === 0) {
this.$msgbox('提示', '取消成功', function () {
that.getOrder()

})
} else {
alert('取消失败')
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},

confirmReceipt (orderGuid) {
let than = this

this.$confirm('提示', '是否确认收货?', function () {
than.getConfirmReceipt(orderGuid)

console.log(orderGuid)
})
},
/* 获取虚拟二维码 */
getQrcode () {


api.qrcode({

vOrderGuid: this.$route.query.vOrderGuid
}).then(res => {
if (res.code === 0) {
this.qrcodeImg = res.data.qrcodeUrl
this.expiresIn = res.data.expiresIn
this.settime(res.data.expiresIn)
}
}, err => {

console.log(err.data.message)
})
},
settime (time) {
let than = this
time--
if (time <= 0) {
than.getQrcode()
} else {
clearTimeout(than.timer)
than.timer = setTimeout(function () {
than.settime(time)

}, 1000)
}
},
intervalQrcode (fnc) {


api.qrcode({

vOrderGuid: this.$route.query.vOrderGuid
}).then(res => {
if (res.code === 0) {
this.qrcodeImg = res.data.qrcodeUrl
this.expiresIn = res.data.expiresIn
fnc && fnc(this.expiresIn)
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},
getConfirmReceipt (orderGuid) {
api.confirm({

vOrderGuid: orderGuid
}).then(res => {
let that = this
if (res.code === 0) {
this.$msgbox('提示', '确认收货成功', function () {

that.getOrder()
})
} else {
console.log('取消失败')
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},
toPay (orderGuid) {


api.doPay({

orderGuid: orderGuid,
orderType: this.orderType
}).then(res => {
console.log('调起支付成功')
console.log(res)
let options = res.data
this.wxPay(options)
}, err => {
console.log('调起支付错误')
this.$msgbox('提示', err.data.message)
console.log(err)
})
},
wxPay (option) {
let than = this
wx.chooseWXPay({

'paySign': option.sign,
'timestamp': option.timestamp,
'nonceStr': option.noncestr,
'package': option.packageValue,
'signType': option.signType,
'success': function (res) {
console.log('支付成功88')
console.log(res)

if (res.errMsg === 'chooseWXPay:ok') {

than.getOrder()
} else if (res.errMsg === 'chooseWXPay:cancel') {
alert('支付取消')
} else if (res.errMsg === 'chooseWXPay:fail') {
alert('支付失败')
}
}
})
},

signApi () {
let urlRe = encodeURIComponent(window.location.href)
api.sign({
url: urlRe
}).then(res => {
console.log('签名res')
console.log(res)
let config = res.data
wx.config({
debug: false,
appId: config.appId,                // 必填，公众号的唯一标识
timestamp: config.timestamp,        // 必填，生成签名的时间戳
nonceStr: config.nonceStr,          // 必填，生成签名的随机串
signature: config.signature,        // 必填，签名，见附录1
jsApiList: config.jsApiList                // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
})














}, err => {
console.log('签名err')
console.log(err)
})
},
signApiShare () {
let urlRe = encodeURIComponent(window.location.href)
let that = this
api.sign({
url: urlRe
}).then(res => {
console.log('签名res')
console.log(res)
let config = res.data
wx.config({
debug: false,
appId: config.appId,                // 必填，公众号的唯一标识
timestamp: config.timestamp,        // 必填，生成签名的时间戳
nonceStr: config.nonceStr,          // 必填，生成签名的随机串
signature: config.signature,        // 必填，签名，见附录1
jsApiList: config.jsApiList                // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
})
wx.ready(function () {
that.setShareProduct(function (res) {
let options = {
title: res.shareTitle,
desc: res.shareContent,
link: res.shareUrl,
imgUrl: res.shareLogo,
success: function () {
console.log('ok')
},
cancel: function () {
console.log('not_ok')
}
}
wx.onMenuShareTimeline(options)
wx.onMenuShareAppMessage(options)
})
})
}, err => {
console.log('签名err')
console.log(err)
})
},
setShareProduct (optionFn) {


api.shareProduct({
vShopProductGuid: this.vShopProductGuid
}).then(res => {
console.log('分享成功2')
console.log(res)
if (res.code === 0) {
optionFn && optionFn(res.data)
}
}, err => {
console.log('分享错误2')
console.log(err)
})
},
...mapMutations({
setvOrderGuid: 'SET_VORDERGUID'
})
},
computed: {
...mapGetters(['vOrderGuid'])
},
components: {
XHeader,
Group,
Cell,
XButton,
HlCell
},
watch: {
}
}



